import React from "react";
import style from "./welcome.module.css";

const WelcomeComponent = ({ start, currentCaseName }) => {
  return (
    <section className={style.main}>
      <h2>{currentCaseName}</h2>
      <button
        onClick={() => {
          start(true);
        }}
      >
        Начать
      </button>
    </section>
  );
};

export default WelcomeComponent;
