import React, { useEffect, useState } from "react";
import style from "./MainResults.module.css";
import { useSelector } from "react-redux";

const VizualResultContainer = ({ result }) => {
  const { questions } = useSelector((state) => state.questions);
  const colors = [
    "rgb(103, 10, 180)",
    "rgb(16, 88, 196)",
    "rgb(182, 110, 16)",
    "rgb(4, 109, 128)",
    "rgb(2, 119, 28)",
  ];

  return (
    <section
      style={{
        background: "black",
        padding: "20px 30px",
        borderRadius: "20px",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-evenly",
        minHeight: "90vh",
      }}
    >
      {questions.map((el, index) => (
        <div key={index}>
          <h2>{el.blockName}</h2>
          <div>
            {el.list.map((li, n) => {
              let count = 0;
              for (let i = 0; i < result.length; i++) {
                if (result[i].list.find((e) => e.name === li.name)) {
                  count++;
                }
              }
              return (
                <div key={n}>
                  {count ? (
                    <div
                      style={{
                        display: "flex",
                        gap: "20px",
                        marginTop: "15px",
                      }}
                    >
                      {li.name}
                      <span
                        style={{
                          display: "block",
                          background: `linear-gradient(-20deg, ${colors[n]}, ${
                            colors[n + 1]
                          })`,
                          width: `${count < 80 ? count * 1 : 80}vw`,
                          textAlign: "center",
                          transition: "2s",
                          borderBottomRightRadius: "20px",
                          borderBottomLeftRadius: "5px",
                          borderTopRightRadius: "5px",
                          borderTopLeftRadius: "20px",
                        }}
                      >
                        {count ? count : null}
                      </span>
                    </div>
                  ) : null}
                </div>
              );
            })}
          </div>
        </div>
      ))}
    </section>
  );
};

export default VizualResultContainer;
