import React from "react";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CSVLink } from "react-csv";
import axios from "axios";
import { getCurrentResult } from "../../DAL/dal";

const Excel = ({ reRender, caseName }) => {
  const questions = useSelector((state) => state.questions);
  const [dataExport, setDataExport] = useState([]);
  const [result, setResult] = useState([]);

  useEffect(() => {
    getCurrentResult().then((r) => {
      setDataExport(r.data);
    });
  }, [reRender]);

  useEffect(() => {
    const array = [];
    questions.questions.map((el) =>
      array.push({ name: el.blockName, list: [] })
    );
    for (let i = 0; i < array.length; i++) {
      for (let j = 0; j < dataExport.length; j++) {
        if (dataExport[j].list[i]) {
          array[i].list.push(dataExport[j].list[i].name);
          // добавляем ответы относящиеся к конкретному вопросу
        }
      }
    }
    const newArray = [];
    for (let i = 0; i < array.length; i++) {
      // фильтруем варианты в новый массив с колличеством подсчитанных ответов
      newArray.push({
        вопросы: array[i].name,
        ...array[i].list.reduce((acc, i) => {
          if (acc.hasOwnProperty(i)) {
            acc[i] += 1;
          } else {
            acc[i] = 1;
          }
          return acc;
        }, {}),
      });
    }
    // console.log(newArray);
    setResult(newArray);
  }, [dataExport, caseName]);

  return (
    <>
      <CSVLink data={result} filename={caseName}>
        Export
      </CSVLink>
    </>
  );
};

export default Excel;
