import React, { useCallback, useEffect, useState } from "react";
import style from "./MainResults.module.css";
import VizualResultContainer from "./VizualResultContainer";
import axios from "axios";
import { getCurrentResult, sendResult } from "../../DAL/dal";

const MainResults = ({ socket, reRender }) => {
  const [middle, setMiddle] = useState([]);
  const [result, setResult] = useState([]);
  const [nextLength, setNextLength] = useState(0);
  const [interval, setInterval] = useState(null);

  let sender = useCallback(() => {
    setInterval(clearTimeout(interval));
    setInterval(
      setTimeout(() => {
        if (result.length && result.length != nextLength) {
          sendResult(result).then((r) => console.log(r));
          console.log(result.length, nextLength);
        }
      }, 1000)
    );
  }, [result]);

  useEffect(() => {
    sender();
  }, [result]);

  useEffect(() => {
    getCurrentResult().then((r) => {
      setResult(r.data);
      setNextLength(r.data.length);
    });
  }, [reRender]);

  useEffect(() => {
    socket.on("pushResult", (data) => {
      setMiddle(data);
      clearTimeout(interval);
    });
  }, [socket]);

  useEffect(() => {
    setResult((prev) => [...prev, ...middle]);
  }, [middle]);

  useEffect(() => {
    console.log(result);
    setNextLength(result.length);
  }, [result]);

  return (
    <section style={{ marginRight: "auto" }}>
      <VizualResultContainer result={result} />
    </section>
  );
};

export default MainResults;
